.servicesBlock{
    background-color: rgb(238, 227, 212);
}

.serviceContainer{
    font-family: 'Raleway', sans-serif;
    max-width: 1200px;
    margin: 0 auto;
    padding: 5px 0;
}

.serviceTitle{
    padding-top: 60px;
    padding-bottom: 50px;
    font-size: 36px;
    text-align: center;
}

.serviceCards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 20px;
    row-gap: 20px;
}

.serviceCard{
    background-color: 	#DAA06D;
    width: 30%;
    text-align: center;
    word-wrap: break-word;
    box-shadow: 0 0 10px #bfbfbf;
}

.serviceCard_foto{
    height: 150px;
    background-repeat: no-repeat;
    background-size: cover;
}

.serviceCard_header2{
    font-size: 36px;
    padding: 5px 0;
}

.serviceCard_header3{
    font-size: 18px;
    padding: 5px 5px 10px 5px;
}

.serviceCard_btn{
    width: 80%;
    padding: 15px;
    background-color: #429062;
    margin-bottom: 15px;
}

@media(max-width: 940px){
    .serviceCard{
        width: 45%;
    }
}

@media(max-width: 600px){
    .serviceCard{
        width: 80%;
    }
}
