.portfoliopageContainer{
    background: white;
    font-family: 'Raleway', sans-serif;
}

.portfoliopageTitle{
    display: flex;
    justify-content: center;
    font-size: 40px;
    padding: 100px 0 0 0;
}

.portfoliopageGallry{
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
    padding: 50px 0;
}

.portfoliopageGallry img{
    width: 600px;
    height: 500px;
}