.line{
    position: relative;
    overflow: hidden;
    min-height: 280px;
    font-family: 'Raleway', sans-serif;
}
.carouselWrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.carouselCard {
    position: absolute;
    transition: transform 1s, opacity 0.4s;
    margin-top: 15px;
    max-width: 240px;
}

.carouselCard h2{
    font-size: 16px;
    font-weight: bold;
    color: white;
    text-align: center;
    
}

.carouselCard img{
    width: 15rem;
    height: 200px;
}

.prevCard{
   transform: translate(-200%, 0);
   opacity: 0;
}

.firstCard{
    transform: translate(-165%, 0);
}
.secondCard{
    transform: translate(-55%, 0);
}
.thirdCard{
    transform: translate(55%, 0);
}
.fourthCard{
    transform: translate(165%, 0);
}

.disabled{
    transform: translate(275%, 0);
    opacity: 0;
}

.arrow-button {
    height: 100%;
    padding: 120px 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    color: #000;
    cursor: pointer;
    background: rgb(255, 255, 255, 0.3);
    width: 50px;
    font-size: 28px;
    display: flex;
    aspect-ratio: 1;
    -webkit-box-shadow: 5px 5px 5px -2px rgba(0, 0, 0, 0.5);
    -moz-box-shadow: 5px 5px 5px -2px rgba(0, 0, 0, 0.5);
    box-shadow: 5px 5px 5px -2px rgba(0, 0, 0, 0.5);
}

span {
    height: 100%;
    width: 100%;
}

.left-arrow {
    left: 0px;
}

.right-arrow {
    right: 0px;
}

.portfolioCard{
    box-shadow: 0px 0px 7px #666;
    position: absolute;
    width: 600px;
    height: 400px;
    margin: 0 10px;
    transform: translate(0%, 0%);
    transition: transform 0.5s linear, opacity 0.3s ease-in-out, width 0.5s linear, height 0.5s linear;
}
.portfolio_prev{
    width: 300px;
    height: 150px;
    transform: translate(-180%, 0%);
    opacity: 1;
}

.portfolio_next{
    width: 300px;
    height: 150px;
    transform: translate(180%, 0%);
    opacity: 1;
}

.portcurrnetCard{
    box-shadow: 0px 0px 0px #666;
    opacity: 1;
    z-index: 2;
}

.portcurrnetCard img{
    position: absolute;
    box-shadow: 0px 0px 7px #666;
}

.portfolio_nexthidden{
    width: 300px;
    height: 150px;
    opacity: 0;
    transform: translate(220%, 0%);
}
.portfolio_prevhidden{
    width: 300px;
    height: 150px;
    transform: translate(-220%, 0%);
    opacity: 0;
}
.portfolioSlider_btn{
    display: flex;
    justify-content: center;
    row-gap: 10px;
    column-gap: 10px;

}

.portfolioSlider_btn button{
    background: rgba(247, 173, 89, 0.281);
    border-radius: 50%;
    padding: 15px;
    font-weight: bold;
    
}

@media(max-width: 1200px){
    .firstCard{
        transform: translate(-110%, 0);
    }
    .secondCard{
        transform: translate(0%, 0);
    }
    .thirdCard{
        transform: translate(110%, 0);
    }
    .fourthCard{
        transform: translate(165%, 0);
        opacity: 0;
    }
}

@media(max-width: 1100px){
    .portfolio_prev{
        opacity: 0;
    }
    .portfolio_next{
        opacity: 0;
    }
}

@media(max-width: 800px){
    .firstCard{
        transform: translate(-60%, 0);
    }
    .secondCard{
        transform: translate(60%, 0);
    }
    .thirdCard{
        transform: translate(110%, 0);
        opacity: 0;
    }

}

@media(max-width: 680px) {
    .portcurrnetCard{
        width: 400px;
        height: 267px;
    }
    .portfolioCards {
        height: 350px !important;
    }
}

@media(max-width: 540px){
    .firstCard{
        transform: translate(0%, 0);
    }
    .secondCard{
        transform: translate(60%, 0);
        opacity: 0;
    }

    .arrow-button{
        width: 30px;
    }
}

@media(max-width: 450px){
    .portcurrnetCard{
        width: 300px;
        height: 200px;
    }
    .portfolioCards {
        height: 300px !important;
    }
}