.wave{
    height: 150px;
    background-image: url('./../../../photo/wave.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.waverotate{
    height: 250px;
    background-image: url('./../../../photo/wave.png');
    background-repeat: no-repeat;
    background-size: cover;
    transform: rotate(180deg);
}