.sUnittablepackegname{
    padding: 15px 0;
    color: white;
    background-color: rgb(19, 16, 16);
    display: flex;
    font-size: 30px;
    justify-content: center;
}

.sUnitpricesblockTitle{
    display: flex;
    justify-content: center;
    font-size: 40px;
    margin-bottom: 30px;
}

.sUnittablecontain{
    background-color: rgb(175, 175, 175);
    font-size: 24px;
    padding: 0 5px;
}
.sUnitprices{
    background: rgb(255,255,255);
background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 3%, rgba(245,238,229,1) 6%, rgba(243,235,225,1) 8%, rgba(238,227,212,1) 100%);
    padding-top: 120px;
}
.sUnittables{
    align-items: baseline;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
}

.sUnittable{
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    font-family: 'Raleway', sans-serif;
    margin: 0 5px;
    min-width: 310px;
    max-width: 400px;
}

.sUnittableneed{
    margin-left: 25px;
    list-style: inside;
}

.sUnittableneed li{
    list-style: inside;
    margin: 15px 0;
}

.sUnittablehavesubject{
    padding-top: 25px;
}

.sUnittableneedsubject{
    padding-top: 15px;
}

.sUnitbonusinfo{
margin: 0 15px;
}

.sUnittableneed li:last-child{
    margin: 0;
    padding-bottom: 15px;
}

.sUnittablebonusinfo{
    margin-top: 50px;
    padding-bottom: 15px;
}

.sUnitafterwords{
    padding-bottom: 30px;
}
.sUnitafterwords li + li{
margin-top: 7px;
}

.sUnitafterwords li:first-child{
    margin-top: 15px;
}

.sUnittablebonusinfo li + li{
    margin-top: 10px;
}