.serviceunittop{
    height: 350px;
    background-repeat: no-repeat;
    background-size: 100%  350px;
}

.sUnittopTitle{
    height: 350px;
    padding: 150px 0;
    font-size: 40px;
    font-weight: 700;
    color: rgb(0, 255, 0);
    display: flex;
    justify-content: center;
    text-align: center;
}
.sUnitmainbox{
    min-height: 650px;
    max-width: 1400px;
    margin: 0 auto;
}
.sUnitmaininfo{
    margin: 20px 0;
}

.sUnittext{
    max-width: 600px;
}

.sUnitTitle{
    margin: 16px 0 16px 24px;
    font-size: 24px;
    font-weight: 400;
    color: black;
}

.serviceunit{
    font-family: 'Raleway', sans-serif;
}

.sUnittextandfoto{
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
    row-gap: 30px;
    column-gap: 30px;
}

.sUnitservicetext{
    text-align: justify;
    font-size: 20px;
    margin: 0px 5px 0 7px;
}

.sUnitservicetext + .sUnitservicetext{
    margin-top: 15px;
}

.sUnitimg img + img{
    margin-top: 15px;
}

.sUnitimg img{
    margin-right: 4px;
    display: block;
}

@media(max-width: 1140px){
    .sUnitimg{
        justify-content: center;
        display: flex;
        row-gap: 10px;
        column-gap: 10px;
        flex-wrap: wrap;
    }

    .sUnitimg img{
        margin-left: 10px;
    }

    .sUnitimg img + img{
        margin-top: 0px;
    }
}
