@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Poppins:wght@400;500;700&family=Raleway&display=swap');

.advantageContainer{
    font-family: 'Raleway', sans-serif;
    max-width: 1200px;
    margin: 0 auto;
}
.advantageTitle{
    display: flex;
    justify-content: center;
    padding: 40px 0;
    text-align: center;
}

.advantageHeader{
    font-size: 42px;
    font-weight: 700;
}

.advantageBlock{
    padding: 10px 10px 0 10px;
}

.advantageCards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 20px;
    row-gap: 20px;
}

.advantageCard{
    width: 45%;
    text-align: center;
    word-wrap: break-word;
}

.advantageCard_icon{
    width: 100px;
    height: 100px;
    margin: 0 auto;
}

.advantageCard_header2{
    font-size: 36px;
    padding: 5px 0;
}

.advantageCard_header3{
    font-size: 18px;
    padding: 5px 5px 10px 5px;
}

@media(max-width: 940px){
    .advantageCard{
        width: 30%;
    }
    .advantageCard_icon{
        width: 75px;
        height: 75px;
    }
}

@media(max-width: 620px){
    .advantageCard{
        width: 80%;
    }
    .advantageCard_icon{
        width: 100px;
        height: 100px;
    }
}