.portfolioContainer{
    max-width: 1200px;
    margin: 0 auto;
    padding-bottom: 30px;
    font-family: 'Raleway', sans-serif;
}

.portfolioblock{
    background-color: rgb(238, 227, 212);
}

.portfolioCards{
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
    transition: all 0.5s linear;

}

.portfolioCard img{
    width: 100%;
}

.portfolioTitle{
    padding-top: 60px;
    padding-bottom: 50px;
    font-size: 36px;
    text-align: center;
}

.portfoliobutton{
    text-align: center;
    margin-top: 30px;
}

.portfoliobtn{
    width: 30%;
    min-width: 240px;
    padding: 15px;
    background-color: #429062;
}