.headerblock{
    min-height: 700px;
    height: 100%;
    background-image: url('./../../../photo/headerfoto.JPG');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    font-family: 'Raleway', sans-serif;
    
}

.headercontainer{
    width: 1200px;
    margin: 0 auto;
}

.headerslider{
    margin-top: 50px;
    margin-bottom: 20px;
    width: 100%;
    background-color: rgba(61, 47, 47, 0.85);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.headerbox{
    color: white;
    max-width: 400px;
    min-width: 100px;
    background-color: rgba(61, 47, 47, 0.85);
    padding: 5px;
    margin-top: 5px;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 10px;
    row-gap: 10px;
}

.headerbox_h1{
    margin-top: 15%;
    font-size: 36px;
    font-weight: 700;
}

.headerbox_h2{
    font-size: 24px;
    font-weight: 400;
    margin-top: 10%;
    margin-bottom: 5%;
}

.headerbox_btn{
    font-size: 24px;
    padding: 20px 30px;
    background-color: #429062;
    margin-bottom: 2%;
    max-height: 100px;
}