.aboutusBlock{
    max-width: 1200px;
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
}

.aboutusTitleheader{
    padding-top: 60px;
    padding-bottom: 50px;
    font-size: 36px;
    text-align: center;
}

.aboutusMainbox{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.aboutusPhoto{
    background-image: url('./../../../photo/aboutus.jpg');
    background-size: contain;
    background-repeat: no-repeat;
    height: 650px;
    width: 430px;

}

.aboutsusTextbox{
    max-width: 500px;
    padding: 0 10px;
}

.aboutsusTextbox__title{

    margin-bottom: 10px;
    font-size: 36px;
    font-weight: 800;
}

.aboutsusTextbox__abzac{
    font-size: 16px;
}

.aboutsusTextbox__abzac + .aboutsusTextbox__abzac{
    margin-top: 7px;
}

.aboutsusTextbox__abzac p{
    margin: 8px 0;
    text-align: justify
}

@media(max-width: 929px){
    .aboutsusTextbox__title{
        text-align: center;
    }
}

@media(max-width: 350px){
    .aboutusPhoto{
        height: 550px;
    }
}