.contactContainer{
    max-width: 1200px;
    margin: 0 auto;
    font-family: 'Raleway', sans-serif;
}

.contactblock{
    background-image: url('./../../../photo/contactbg.png');
    background-repeat: no-repeat;
    background-size: cover;
}
.contactdataUL a{
   color: black;
   transition: all 0.2s;
}

.contactdataUL a:hover{
    color: blue; 
}

.contactdataLi{
    display: flex;
    margin: 20px 0;
}

.contactdataLiimg{
    width: 30px;
    height: 30px;
    margin-right: 10px;
}
.contactdataLitextTitle{
    font-size: 28px;
}

.contactdataLitextMain{
    margin-left: 7px;
    font-size: 20px;
}

.contactTitle{
    padding-top: 60px;
    padding-bottom: 50px;
    font-size: 40px;
    text-align: center;
    font-weight: bold;
}
.contactformTitle{
    text-align: center;
    font-size: 36px;
    margin-bottom: 40px;
}
.contactMainbox{
    display: flex;
    flex-wrap: wrap;
    row-gap: 10px;
    column-gap: 10px;
    justify-content: space-around;
}
.contactdataTitle{
    font-size: 36px;
    margin-top: 20px;
    margin-bottom: 15px;
}
.contactForm{
    min-height: 500px;
    border-radius: 50px;
    background-color: white;
    max-width: 460px;
    padding: 20px;
    margin-bottom: 30px;
}
.contactformInputs{
    padding: 15px;
    width: 90%;
    margin: 10px 0 10px 5px;
    border: 1px solid black;
    border-radius: 5px;
}

.contactformButton{
    margin: 20px auto 30px auto;
    border-radius: 15px;
    width: 60%;
    display: block;
    padding: 15px;
    background-color: #429062;
    transition: all 0.3s;
}

.contactformButton:hover{
    background-color: green;
}

.contactformTextarea{
    resize: none;
    padding: 5px 0 0 7px;
    width: 90%;
    margin: 10px 0 10px 5px;
    border: 1px solid black;
    border-radius: 5px;
    height: 180px;
}
.contactdatablock{
    max-width: 400px;
    width: 100%;
}